'use client'
import { useState } from 'react';
import { Search } from 'lucide-react';

const Page = () => {
  return (
    <main className="min-h-screen p-6">
      {/** Título de la página **/}
      <h1 className="font-bold text-3xl mb-5">Products List</h1>

      {/** Tabla **/}
      <div className="flex flex-col w-full">
        <div className="overflow-x-auto"> {/*para evitar overflow*/}
          <div className="inline-block min-w-full py-2 sm:px-6 lg:px-8"> {/*para el desplazamiento horizontal*/}
            <div className="overflow-hidden">

              {/** Buscador */}
              <div className="bg-[#E96000] rounded-t-md h-14 flex items-center p-4 relative"> {/* Posicionamiento relativo */}
                <input
                  placeholder="Buscar..."
                  className="w-full rounded-full py-2 px-10 focus:outline-none focus:ring-2 focus:ring-orange-400" // Padding ajustado
                />
                <div className="absolute inset-y-0 right-3 flex items-center pointer-events-none">
                  <SearchIcon className="h-5 w-5 text-gray-500" />
                </div>
              </div>

              <table className="min-w-full text-left text-sm font-light text-surface dark:text-white">
                <thead className="border-b-4 border-neutral-200 bg-[#E96000] text-white">
                  <tr>
                    <th scope="col" className=" px-6 py-4">#</th>
                    <th scope="col" className=" px-6 py-4">First</th>
                    <th scope="col" className=" px-6 py-4">Handle</th>
                    <th scope="col" className=" px-6 py-4">Handle</th>
                  </tr>
                </thead>
                <tbody>
                  <tr
                    className="border-b border-neutral-200 transition duration-300 ease-in-out hover:bg-neutral-100 dark:border-white/10 dark:hover:bg-neutral-600">
                    <td className="whitespace-nowrap px-6 py-4 font-medium">1</td>
                    <td className="whitespace-nowrap px-6 py-4">Mark</td>
                    <td className="whitespace-nowrap px-6 py-4">Otto</td>
                    <td className="whitespace-nowrap px-6 py-4">@mdo</td>
                  </tr>
                  <tr
                    className="border-b border-neutral-200 transition duration-300 ease-in-out hover:bg-neutral-100 dark:border-white/10 dark:hover:bg-neutral-600">
                    <td className="whitespace-nowrap px-6 py-4 font-medium">2</td>
                    <td className="whitespace-nowrap px-6 py-4">Jacob</td>
                    <td className="whitespace-nowrap px-6 py-4">Thornton</td>
                    <td className="whitespace-nowrap px-6 py-4">@fat</td>
                  </tr>
                  <tr
                    className="border-b border-neutral-200 transition duration-300 ease-in-out hover:bg-neutral-100 dark:border-white/10 dark:hover:bg-neutral-600">
                    <td className="whitespace-nowrap px-6 py-4 font-medium">3</td>
                    <td className="whitespace-nowrap px-6 py-4">Larry</td>
                    <td className="whitespace-nowrap px-6 py-4">Wild</td>
                    <td className="whitespace-nowrap px-6 py-4">@twitter</td>
                  </tr>
                </tbody>
              </table>


            </div>
          </div>
        </div>
      </div>
    </main>
  );
};
export default Page;